import axios from 'axios'
import { jwtDecode } from 'jwt-decode'
import dayjs from 'dayjs'
import { getToken } from './Global'

// const BackEndBaseLink = 'http://localhost:8080/api/'
// const BackEndBaseLink = 'http://216.48.177.166/pmtool/'
const BackEndBaseLink = 'https://testing_demo.cavinkare.in/test_neram/'

// const subPath = '/'
const subPath = '/neram'
// const subPath = '/demo/product_management'

const createAxiosInstance = (token) => {
  return axios.create({
    baseURL: BackEndBaseLink,
    headers: token ? { Authorization: `Bearer ${token}` } : {},
  })
}

const isTokenExpired = (exp) => {
  return dayjs.unix(exp || 0).diff(dayjs()) < 1
}

const isRefreshTokenExpired = (decoded, refreshToken) => {
  const isRefreshExpired = isTokenExpired(decoded?.exp)
  if (isRefreshExpired || !refreshToken) {
    logoutSession()
  }
}

const useAxios = () => {
  const authTokens = getToken()
  const token = authTokens.Token
  const decoded = token ? jwtDecode(token) : {}
  const refresDecoded = authTokens.RefreshToken ? jwtDecode(authTokens.RefreshToken) : {}

  const axiosInstance = createAxiosInstance(token)
  axiosInstance.interceptors.request.use(async (req) => {
    const isSignInRequest = req.url.includes('auth/signin')
    if (isSignInRequest) return req
    const refreshToken = authTokens.RefreshToken
    isRefreshTokenExpired(refresDecoded, refreshToken)
    if (!isTokenExpired(decoded.exp)) return req
    const response = await axios.post(
      `${BackEndBaseLink}auth/refreshtoken?refreshToken=${refreshToken}&accessToken=${token}`,
    )
    if (response.status === 200) {
      response.data.data.RefreshToken = refreshToken
      localStorage.setItem('userData', JSON.stringify(response.data.data))
      localStorage.setItem('Pm_tool', 'true')
      const logoutState = localStorage.getItem('logout')
      if (logoutState !== null) {
        localStorage.setItem('logout', logoutState)
      }
      req.headers.Authorization = `Bearer ${response.data.data.Token}`
      return req
    } else {
      logoutSession()
    }
  })
  return axiosInstance
}

const logoutSession = () => {
  window.localStorage.clear()
  localStorage.removeItem('userData')
  localStorage.removeItem('logout')
  localStorage.removeItem('Pm_tool')
  localStorage.removeItem('neram')
  localStorage.clear()
  window.location.href = subPath
}

export default useAxios
